<template>
  <section id="about" class="section">
    <div class="container has-text-centered">
      <div class="image avatar">
        <img src="../assets/img/samrose.jpg">
      </div>
      <separator></separator>
      <div class="about-text block">
        Saya Riki Setiyo Pambudi,
        Saya Seorang front-end dan Beck-End Developer
        Dari dulu sampai sekarang Error adalah teman saya dan dari situ saya bisa belajar 
        Saya Lahir Di kota terpencil Ngawi Jawa timur , Kelahiran Pada 22 September 1999
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'about',
  data() {
    return {};
  },
  mounted() {
    sr.reveal('#about .block', 200);
  },
};
</script>

<style lang="scss">
  @import '~vars';

  .avatar {
    height: 200px;
    width: 200px;
    margin: 1rem auto;

    img {
      border-radius: 200px;
      border: 3px solid $secondary;
    }
  }

  .about-text {
    max-width: $tablet;
    margin: auto;
  }
</style>



